header{
    display: flex;
    align-items: center;
    justify-content: space-around;

    font-family: 'Times New Roman', Times, serif;
    font-weight: bold;
    color: white;


    background-color: #43241f;
}

header .title{
    font-size: larger;
}

ul{
    display: flex;
    list-style-type: none;
}

ul li{
    text-align: center;
}

ul li a{
    display: block;
    width: 100px;

    text-decoration: none;

    color: white;
}

